button {
	padding: .75rem 1.75rem;
	border-radius: 5px;
	background-color: #B8B42D;
	color: #fff;
	font-size: 1.2rem;
	-webkit-transition: -webkit-transform ease-in 50ms;
	transition: -webkit-transform ease-in 50ms;
	-o-transition: transform ease-in 50ms;
	transition: transform ease-in 50ms;
	transition: transform ease-in 50ms, -webkit-transform ease-in 50ms;
	display: block;
	margin: auto;
	-webkit-box-shadow: 2px 4px 5px #dedede;
	        box-shadow: 2px 4px 5px #dedede;
}

button:hover {
	cursor: pointer;
	-webkit-transform: scale(1.02);
	    -ms-transform: scale(1.02);
	        transform: scale(1.02);
}


