.code-selector {
	padding: 15px;
	border-radius: 3px;
	width: 200px;
	margin: 10px;
	background-color: #DD403A;
	color: #FFFCE8;
	opacity: .9;
}

.code-selector:hover {
	cursor: pointer;
	-webkit-transform: scale(1.02);
	    -ms-transform: scale(1.02);
	        transform: scale(1.02);
	opacity: 1;
}

h1 {
	color: red;
}

.selection-grid {
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	text-align: center;
	padding: 20px;
}

@media screen and (max-width: 600px){
	.selection-grid {
		-ms-flex-pack: center;
		    justify-content: center;
		padding: 0px;
	}

}


